import Vue from 'vue';
import Vuex from 'vuex';

import app from './modules/app';

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    app,
  },
  getters: {
    pushTimeType: state => state.app.pushTimeType,
    
    warnTypeLabel: state => state.app.warnTypeLabel,
    warnType: state => state.app.warnType,
    warnStatus: state => state.app.warnStatus,

    equipSelList: state => state.app.equipSelList,
    tableList: state => state.app.tableList,

    deviceType: state => state.app.deviceType
  }
});



