<template>
  <div id="add-defect">
    <div class="nav-wp">
      <van-nav-bar title="新增缺陷" @click-left="goBack" class="nav-bar">
        <div class="goback-btn-wp" slot="left">
          <img src="../../zong-fanhui.png" alt="" class="goback-btn">
        </div>
      </van-nav-bar>
    </div>
    <div class="main-wp">
      <van-cell-group>
        <van-field class="input-cell" v-model="submitter" placeholder="请输入提交人">
          <div slot="label">
            <img class="input-label-icon" src="../../tijiaoren-.png" alt="">
          </div>
        </van-field>
      </van-cell-group>
      <van-cell-group>
        <van-field class="input-cell" v-model="devName" placeholder="请选择终端名称">
          <div slot="label">
            <img class="input-label-icon" src="../../zhongduan.png" alt="">
          </div>
        </van-field>
      </van-cell-group>
      <van-cell-group>
        <van-field class="input-cell" v-model="defectLevelValue" placeholder="请选择缺陷等级" readonly @click="pickDefectLevel">
          <div slot="label">
            <img class="input-label-icon" src="../../quexian-.png" alt="">
          </div>
        </van-field>
      </van-cell-group>
      <van-cell-group>
        <van-field class="input-cell" v-model="defectTypeValue" placeholder="请选择缺陷类型" readonly @click="pickDefectType">
          <div slot="label">
            <img class="input-label-icon" src="../../quexian-.png" alt="">
          </div>
        </van-field>
      </van-cell-group>
      <van-cell-group>
        <van-field class="input-cell" v-model="deadline" placeholder="请选择最迟消缺时间" readonly @click="pickDeadline">
          <div slot="label">
            <img class="input-label-icon" src="../../xiaoqueshijian.png" alt="">
          </div>
        </van-field>
      </van-cell-group>
      <div class="defect-desc-wp">
        <div class="input-label-icon-wp">
          <img class="input-label-icon" src="../../miaoshu.png" alt="">
        </div>
        <van-cell-group>
          <van-field v-model="defectDesc" type="textarea" placeholder="请输入缺陷描述" rows="2" autosize>
          </van-field>
        </van-cell-group>
      </div>
      <div class="live-pic-wp">
        <div class="label-wp">
          <img class="input-label-icon" src="../../baoxiu.png" alt="">
          <span class="title">报修现场照片/视频</span>
        </div>
        
        <div class="upload-btn">
          <!-- <van-uploader :after-read="imgRead" :before-read="imgBeforeRead" accept="image/jpg, image/jpeg, image/png" :result-type="test">
            <img src="../../shangchuan.png" alt="" class="upload-img">
          </van-uploader> -->
        </div>
      </div>
      <div class="submit-btn-wp" @click="addDefectInfo">提交</div>
    </div>
    <!-- 时间选择器 -->
    <van-popup v-model="timePickerShow" position="bottom" class="timepicker-popup">
      <van-datetime-picker v-model="timePicker.date" type="datetime" :min-date="timePicker.minDate" :max-date="timePicker.maxDate" @confirm="pickTimeConfirm" @cancel="pickTimeCancel"/>
    </van-popup>
    <!-- 缺陷等级选择 -->
    <van-popup v-model="defectLevelPickerShow" position="bottom" class="">
      <van-picker :columns="defectLevelList" value-key="label" @confirm="defectLevelPickConfirm" @cancel="defectLevelPickCancel" :show-toolbar="true"/>
    </van-popup>
    <!-- 缺陷类型选择 -->
    <van-popup v-model="defectTypePickerShow" position="bottom" class="">
      <van-picker :columns="defectTypeList" value-key="label" @confirm="defectTypePickConfirm" @cancel="defectTypePickCancel" :show-toolbar="true"/>
    </van-popup>
  </div>
</template>

<script>
import _util from '@/util/index';

const MIN_DATE = new Date(2018, 1, 1);
const MAX_DATE = new Date();

const defectLevelList = [
  {
    label: '一般',
    value: 1
  },
  {
    label: '紧急',
    value: 2
  }
]

const defectTypeList = [
  {
    label: '终端长期离线',
    value: 5
  },
  {
    label: '终端频繁投退',
    value: 6
  },
  {
    label: '开关遥控失败',
    value: 7
  },
  {
    label: '信号抖动',
    value: 8
  },
  {
    label: '数据缺漏异常',
    value: 9
  },
  {
    label: '电源故障',
    value: 10
  },
    {
    label: '蓄电池故障',
    value: 11
  },
  {
    label: '流量越限',
    value: 12
  },
  {
    label: '其他',
    value: 99
  }
]

export default {
  data() {
    return {
      submitter: '',
      devName: '',
      defectLevel: '',
      defectType: '',
      deadline: '',
      defectDesc: '',

      hasImg: false,
      imgReadOK: false, 

      timePickerShow: false,
      timePicker: {
        minDate: MIN_DATE,
        maxDate: MAX_DATE,
      },

      defectLevelList,
      defectLevelPickerShow: false,
      defectLevelValue: '',

      defectTypeList,
      defectTypePickerShow: false,
      defectTypeValue: '',
    }
  },
  methods: {
    goBack(){
      this.$router.replace({path: '/four'});
    },

    // 最迟消缺时间选取相关
    pickDeadline() {
      this.timePickerShow = true;
    },
    pickTimeConfirm(value) {
      this.deadline = _util.dashDateTimeWithoutSpace(value);
      this.timePickerShow = false;
    },
    pickTimeCancel() {
      this.timePickerShow = false;
    },

    // 缺陷等级选取相关
    pickDefectLevel(){
      this.defectLevelPickerShow = true;
    },
    defectLevelPickConfirm(item) {
      this.defectLevel = item.value;
      this.defectLevelValue = item.label,
      this.defectLevelPickerShow = false;
    },
    defectLevelPickCancel() {
      this.defectLevelPickerShow = false;
    },

    // 缺陷类型
    pickDefectType() {
      this.defectTypePickerShow = true;
    },
    defectTypePickConfirm(item) {
      this.defectType = item.value;
      this.defectTypeValue = item.label,
      this.defectTypePickerShow = false;
    },
    defectTypePickCancel() {
      this.defectTypePickerShow = false;
    },

    // 新增缺陷
    addDefectInfo() {
      let req = {
        defectType: this.defectType,
        defectLevel: this.defectLevel,
        lastHandleTime: this.deadline,
        describe: this.defectDesc,
        confirmUser: this.submitter
      }
      console.log(req);
      // return new Promise((resolve, reject) => {
      //   defectApi.addDefectInfo(data).then(res => {
      //     console.log(res);
      //     resolve();
      //   }).catch(err => {
      //     // console.log(err);
      //     reject(err);
      //   })
      // })
    },
    // 文件读取前；
    imgBeforeRead() {
      this.hasImg = true;
      this.imgReadOK = false;
    },
    // 文件读取完成
    imgRead(file) {
      this.hasImg = true;
      this.imgReadOK = true;
    }
  },
}
</script>

<style lang="scss">
@import "@/common/scss/index.scss";

#add-defect{
  .nav-wp{
    height: 48px;
  }
  @extend %nav-with-one-title;
  .main-wp{
    margin: 0 15px;
    .input-cell{
      padding: 0 0 0 5px;
      height: 50px;
      line-height: 50px;
      font-size: 14px;
      color: #666;
    }
    .van-cell__title{
      max-width: 35px;
    }
    .van-field__control{
      color: #666;
    }
    .input-label-icon{
      width: 16px;
      height: 16px;
      vertical-align: middle;
    }
    input::-webkit-input-placeholder{
      color: #c7c7c7;
    }
    textarea::-webkit-input-placeholder{
      color: #c7c7c7;
    }
    .defect-desc-wp {
      .van-cell-group{
        &::after{
          content: none;
        }
      }
      .van-cell{
        border: 1px solid #d7d7d7;
        border-radius: 3px;
      }  
    }
    .input-label-icon-wp {
      padding-left: 5px;
      height: 50px;
      line-height: 50px;
    }
    .live-pic-wp{
      margin-top: 20px;
      .label-wp{
        margin-left: 5px;
        font-size: 0;
        .input-label-icon{
          margin-right: 15px;
        }
        .title{
          vertical-align: middle;
          font-size: 14px;
          color: #666;
        }
      }
      .upload-btn{
        margin-top: 15px;
        
        .upload-img{
          border-radius: 3px;
          border: 1px dashed #999;
          width: 80px;
          height: 80px;
        }
      }
    }
    @extend %submit-btn;
  }
  .timepicker-popup {
    width: 100%;
  }
}
</style>


